    </div>

    <div style="padding: 20px"  :style="{ display: display[2] }">
      <h4>Sentence 3</h4>
      <form class="checkbox-rect2">
        <template v-for="item in sentence_three">
          <div :class="{selected: item.selected, unselected: !item.selected}">
