        </template>
      </form>
    </div>

    <div style="padding: 20px"  :style="{ display: display[3] }">
      <h4>Sentence 4</h4>
      <form class="checkbox-rect2">
        <template v-for="item in sentence_four">
          <div :class="{selected: item.selected, unselected: !item.selected}">
            <input type="checkbox" :id="item.id" v-model="item.selected"/>
            <label :for="item.id">{{ item.value }}</label>
          </div>
        </template>
      </form>
    </div>
    <footer>
      <div id="buttons" class="relative">
        <button class="navitem" @click="validate_one()" v-if="currentStep === 1">Next</button>
        <button class="navitem" @click="validate_two()" v-if="currentStep === 2">Next</button>
        <button class="navitem" @click="validate_three()" v-if="currentStep === 3">Next</button>
        <button class="navitem" @click="validate_four()" v-if="currentStep === 4">Next</button>
      </div>
    </footer>
  </main>
</template>


<script>
export default {
