    return {
      valid: true,
      one: false,
      two: false,
      three: false,
      four: false,
      currentStep: 1,
      display: ["block", "none", "none", "none", "none"],
      sentence_one: [
        {serial: 0, id: 0, value: "A", selected: false},
        {serial: 1, id: 1, value: "customer", selected: false},
        {serial: 2, id: 2, value: "comes", selected: false},
        {serial: 3, id: 3, value: "to", selected: false},
        {serial: 4, id: 4, value: "the", selected: false},
        {serial: 5, id: 5, value: "office", selected: false},
        {serial: 6, id: 6, value: "to", selected: false},
        {serial: 7, id: 7, value: "acquire", selected: false},
        {serial: 9, id: 8, value: "a", selected: false},
        {serial: 10, id: 9, value: "vehicle", selected: false},
      ],
      sentence_two: [
        {serial: 0, id: 10, value: "The", selected: false},
        {serial: 1, id: 11, value: "clerk", selected: false},
        {serial: 2, id: 12, value: "locates", selected: false},
        {serial: 3, id: 13, value: "the", selected: false},
        {serial: 4, id: 14, value: "vehicle reservation contract", selected: false},
        {serial: 5, id: 15, value: "by", selected: false},
        {serial: 6, id: 16, value: "means", selected: false},
        {serial: 7, id: 17, value: "of", selected: false},
        {serial: 8, id: 18, value: "the", selected: false},
